import React, {useState, useEffect} from 'react';
import icon from '../../icons/vaagana.png';

const IMac = (props)=>{

    let appContent = [];
    const renderApp = (data, index)=>{
      return(
        <li onClick={()=>(props.handle(index))}>
        <span>{data.name}</span>
        <a ><img src={icon}/></a>
      </li>
        );
    }
    
    const renderApps = ()=>{
        props.app.forEach((data,index)=>{
            appContent.push(renderApp(data,index));
        });
         return appContent;
      }

return(
    <div id="dock-container" class="right-align" >
 <div id="dock" >
   <ul>
     {renderApps()}
   </ul>
 </div>
</div>
);
};


export default IMac;